
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
components: {
Confirm
},
data(){
return {
sendMsg: '',
errorMsg: '',
timerFinish: true,
phoneNum: '',
code: '',
showLoading: false,
showAlert: false,
showToast: false,
toastText: ''
}
},
methods: {
sendCode(){
if(this.timerFinish && this.phoneNum != ''){
this.timerFinish = false;
let count = 60;
this.sendMsg = `${--count}s`;
let timer = setInterval(()=>{
count--;
this.sendMsg = `${count}s`;
if(count <= 0){
clearInterval(timer);
this.timerFinish = true;
this.sendMsg = '';
}
},1000);

this.$http.post("/wx/verify", {cellphone: this.phoneNum}, {
success: (data)=> {
console.log(data);
},
error: (error)=> {
console.log("error", error);
if(error.error){
this.toastText = error.error;
this.showToast = true;
}
}
})
}else{

}
},
login(){
if(this.code != ''){
this.showLoading = true;
this.$http.post("/wx/verify/submit", {verify_code: this.code}, {
success: (data)=> {
this.showLoading = false;
this.redirectAfterLogin(data);
},
error: (error)=> {
this.showLoading = false;
this.loginError(error);
}
});
}
},
onConfirm(){
window.location.href = `${process.env.HOST}?target=yyb`;
},
getQueryString(name){
const reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
let r = window.location.search.substr(1).match(reg);
if (r !== null) return decodeURIComponent(r[2]); return null;
},
redirectAfterLogin(data){
if(data.account && data.users && data.users.length > 0){
window.localStorage.setItem('phone', data.account.cellphone);
window.localStorage.setItem('data', JSON.stringify(data));
if(this.$router.history.current.query.redirect){
let redirect = this.$router.history.current.query.redirect;
this.$router.replace(redirect);
}else{
this.$router.replace('report');
}
}else{
this.showAlert = true;
window.localStorage.removeItem('phone');
window.localStorage.removeItem('data');
}
},
loginError(error){
if(error.error){
this.toastText = error.error
this.showToast = true;
}
window.localStorage.removeItem('phone');
window.localStorage.removeItem('data');
}
},
mounted(){
if(window.sessionStorage.getItem('code') != undefined && window.sessionStorage.getItem('code') != null){

this.$http.post('/wx/account',{code: window.sessionStorage.getItem('code')}, {
success: (data)=> {
if(data.account){
this.redirectAfterLogin(data);
}
},
error: (error)=> {
window.sessionStorage.removeItem('code');
window.sessionStorage.removeItem('state');
}
})
}else if(window.location.href.indexOf('code') > 0){
const wxcode = this.getQueryString('code');
const state = this.getQueryString('state');
if(wxcode !== null && state !== null){
window.sessionStorage.setItem('code', wxcode);
window.sessionStorage.setItem('state', state);

this.$http.post('/wx/account',{code: wxcode}, {
success: (data)=> {
if(data.account){
this.redirectAfterLogin(data);
}
},
error: (error)=> {

}
})
}
}else{
let uri = encodeURIComponent(window.location.href);
let wx = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx86408eb27099d681&redirect_uri=${uri}&response_type=code&scope=snsapi_base&state=123&#wechat_redirect`;

}
}
}



