
import XTable from 'vux/src/components/x-table/index.vue'
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'

export default {
components: {
XTable, Timeline, TimelineItem
},
data(){
return {
showLoading: false,
loadingText: '',
index: 0,
group: [],
showDetail: false,
imgUp: require('../assets/double_up.png'),
imgDown: require('../assets/double_down.png'),
data: [],
summary: {lifeDays: '0', lessonCount: '0', duration: 0, lastweekLifeDays: 0 ,lastweekLessonCount: 0, lastweekMinutes: 0, percent: 0.0},
weekWords:['', '', '', '', '', '', '']
}
},
methods: {
getGroup(){
const data = JSON.parse(window.localStorage.getItem('data'));
const users = data.users;
for(let i = 0; i < users.length; i++){
const user = users[i];
this.group.push({name: user.realname, id: user.id});
}
if(this.group.length > 0){
this.getDetail(this.group[0].id);
}
},
ongroupClick(index){
if(this.index != index){
this.index = index;
this.getDetail(this.group[index].id);
}
},
onDetailClick(){
this.showDetail  = !this.showDetail;
},
onTaskClick(){

},
getDetail(id){
this.showLoading = true;
this.$http.post('/wx/report', {uid: '11'}, {
success: (data)=> {
const sum = data.summary;
this.summary.lifeDays = sum.days;
this.summary.lessonCount = sum.lesson_count;
this.summary.duration = this.toMinutes(sum.duration);
let lastweekLifeDays = 0;
data.report.forEach((item)=> {
if(item.lessons.length > 0){
lastweekLifeDays += 1;
}
});
this.summary.lastweekLifeDays = lastweekLifeDays;
let lastweekLessonCount = 0;
data.report.forEach((item)=> {
item.lessons.forEach((lesson)=> {
lastweekLessonCount += 1;
})
});
this.summary.lastweekLessonCount = lastweekLessonCount;
let lastweekDuration = 0;
data.report.forEach((item)=> {
item.lessons.forEach((lesson)=> {
lastweekDuration += parseInt(lesson.duration);
})
});
lastweekDuration = parseInt(lastweekDuration);
this.summary.lastweekMinutes = this.toMinutes(lastweekDuration);
this.summary.percent = sum.precent;
if(this.data.length > 0){
this.data.splice(0, this.data.length);
}
this.data.push(...data.report);
this.showLoading = false;
},
error: (error)=> {
this.showLoading = false;
this.data.splice(0, this.data.length);
}
})
},
formatDuration(lessons){
var duration = 0;
for(let i = 0; i < lessons.length; i++){
let lesson = lessons[i];
duration += parseInt(lesson.duration);
}
if(duration === 0){
return duration;
}
var seconds = duration / 1000;
if(seconds < 60){
return parseInt(seconds) + '';
}
var minutes = seconds / 60;
if(minutes < 60){
return parseInt(minutes) + '';
}
var hour = minutes / 60;
return hour.toFixed(1) + "";
},
toMinutes(duration){
if(duration === 0){
return duration;
}
var seconds = duration / 1000;
if(seconds < 60){
return parseInt(seconds);
}
var minutes = seconds / 60;
return parseInt(minutes);
},
getTotal(lessons){
var total = 0;
for(let i = 0; i < lessons.length; i++){
total += parseInt(lessons[i].total);
}
return parseInt(total);
},
getCorrect(lessons){
var count = 0;
for(let i = 0; i < lessons.length; i++){
count += parseInt(lessons[i].correct);
}
return parseInt(count);
}
},
mounted(){
this.getGroup();
}
}



