export default {
    data(){
        return {
            phone: '135xxxx3322',
            sendMsg: '',
            timerFinish: true,
        }
    },
    methods: {
        sendCode(){
            if(this.timerFinish){
                this.timerFinish = false;
                let count = 60;
                this.sendMsg = `${--count}s`;
                let timer = setInterval(()=>{
                    count--;
                    this.sendMsg = `${count}s`;
                    if(count <= 0){
                        clearInterval(timer);
                        this.timerFinish = true;
                        this.sendMsg = '';
                    }
                },1000);
                //
            }
        },
        change(){
            
        }
    },
    mounted(){
        let localPhone = window.localStorage.getItem('phone');
        localPhone = localPhone.trim().replace(' ', '');
        this.phone = localPhone.substring(0, 3) + 'xxxx' + localPhone.substring(7, localPhone.length);
    }
}



