
import InlineXSwitch from 'vux/src/components/inline-x-switch/index.vue'
import Range from 'vux/src/components/range/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'

export default {
components: {
InlineXSwitch, Range, PopupPicker, XTable
},
data(){
return {
lock: true,
timeSwitch: true,
allowMins: 20,
clockSwitch: true,
data: [['00:00','01:00','02:00','03:00','04:00','05:00','06:00','07:00','08:00','09:00','10:00','11:00','12:00','13:00','14:00','15:00','16:00','17:00','18:00','19:00','20:00','21:00','22:00','23:00']],
weekClockFromValue: ['21:00'],
weekClockEndValue: ['06:00'],
weekendClockFromValue: ['21:00'],
weekendClockEndValue: ['06:00'],
index: 0,
group: [],
history: [],
showLoading: false,
showToast: false,
toastText: '-.-',
weekWords:['', '', '', '', '', '', '']
}
},
methods: {
onChange(value){
if(this.lock){
console.log("lock");
}else{

this.showLoading = true;
let params = {allow_mins: '0', allow_begin: '0,0', allow_end: '0,0'};
if(this.timeSwitch){
params.allow_mins = `${this.allowMins}`;
}
if(this.clockSwitch){
params.allow_begin = `${this.weekClockFromValue[0]},${this.weekendClockFromValue[0]}`;
params.allow_end = `${this.weekClockEndValue[0]},${this.weekendClockEndValue[0]}`;
}
this.$http.post('/wx/account/update', params, {
success: (data)=> {
this.showLoading = false;
this.toastText = '';
this.showToast = true;
let localdata = JSON.parse(window.localStorage.getItem('data'));
let account = localdata.account;
account.allow_mins = params.allow_mins;
account.allow_begin = params.allow_begin;
account.allow_end = params.allow_end;
localdata.account = account;
window.localStorage.setItem('data', JSON.stringify(localdata));
},
error: (error)=> {
this.showLoading = false;
if(error.error){
this.toastText = error.error;
this.showToast = true;
}
}
})
}
},
ongroupClick(index){
if(this.index != index){
this.index = index;
const id = this.group[index].id;

this.getHistory(id);
}
},
getHistory(id){
this.$http.post('/wx/report', {uid: '11'}, {
success: (data)=>{
if(this.history.length > 0){
this.history.splice(0, this.history.length);
}
this.history.push(...data.report);
},
error: (error)=> {
this.history.splice(0, this.history.length);
}
})
},
formatDuration(lessons){
var duration = 0;
for(let i = 0; i < lessons.length; i++){
let lesson = lessons[i];
duration += parseInt(lesson.duration);
}
if(duration === 0){
return duration;
}
var seconds = duration / 1000;
if(seconds < 60){
return parseInt(seconds) + '';
}
var minutes = seconds / 60;
if(minutes < 60){
return parseInt(minutes) + '';
}
var hour = minutes / 60;
return hour.toFixed(1) + "";
}
},
mounted(){
this.showLoading = true;
this.lock = true;
const data = JSON.parse(window.localStorage.getItem('data'));
const users = data.users;
for(let i = 0; i < users.length; i++){
const user = users[i];
this.group.push({name: user.realname, id: user.id});
}

if(this.group.length > 0){
this.getHistory(this.group[0].id);
}

const account = data.account;
const allowMinutes = account.allow_mins;
const allowBegin = account.allow_begin;
const allowEnd = account.allow_end;
if(allowMinutes === '' || allowMinutes == '0'){
this.timeSwitch = false;
}else{
this.timeSwitch = true;
this.$nextTick(()=>{
this.allowMins = parseInt(account.allow_mins);
})
}
if(allowBegin === '' || allowEnd === ''){
this.clockSwitch = false;
}else if(allowBegin === '0,0' || allowEnd === '0,0'){
this.clockSwitch = false;
}else{
let begin = allowBegin.split(',');
let end = allowEnd.split(',');
this.clockSwitch = true;
this.$nextTick(()=> {
this.weekClockFromValue.splice(0, 1, begin[0]);
this.weekClockEndValue.splice(0, 1, end[0]);
this.weekendClockFromValue.splice(0, 1, begin[1]);
this.weekendClockEndValue.splice(0, 1, end[1]);
})
}
setTimeout(() => {
this.lock = false;
this.showLoading = false;
}, 1000);
}
}



